import React, { Component } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js';
import { EffectComposer } from 'three/addons/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/addons/postprocessing/RenderPass.js';
import { OutputPass } from 'three/addons/postprocessing/OutputPass.js';
import { UnrealBloomPass } from 'three/addons/postprocessing/UnrealBloomPass.js';

function generateSprite() {

  var canvas = document.createElement('canvas');
  canvas.width = 32;
  canvas.height = 32;

  var context = canvas.getContext('2d');
  var gradient = context.createRadialGradient(canvas.width / 2, canvas.height / 2, 0, canvas.width / 2, canvas.height / 2, canvas.width / 2);
  gradient.addColorStop(0, 'rgba(255,22,0,1)');
  gradient.addColorStop(0.1, 'rgba(255,42,0,1)');
  gradient.addColorStop(0.2, 'rgba(255,128,40,1)');
  gradient.addColorStop(0.4, 'rgba(123,0,128,1)');
  gradient.addColorStop(.8, 'rgba(128,0,128,.5)');
  gradient.addColorStop(1, 'rgba(128,0,128,.0)');

  context.fillStyle = gradient;
    context.beginPath();
    context.arc(canvas.width / 2, canvas.height / 2, canvas.width / 2, 0, 2 * Math.PI);
    context.fill();

  var texture = new THREE.Texture(canvas);
  texture.needsUpdate = true;

  // canvas.style = 'width: 500px; height: 500px; position: fixed; top: 0;'
  // document.body.appendChild(canvas);
  return texture;

}

var originalPos;
var dirs;
function updatePointsGeometry(points, offset) {
  if (!originalPos)  {
    for (var i = 0; i < points.geometry.attributes.position.array.length; i+=3) {
      points.geometry.attributes.position.array[i+0] = Math.random() * 20 - 10;
      points.geometry.attributes.position.array[i+1] = Math.random() * 20 - 10;
      points.geometry.attributes.position.array[i+2] = Math.random() * 20 - 10;
    }
    originalPos = points.geometry.attributes.position.array.slice(0);
  }
  else {
    for (var i = 0; i < originalPos.length; i+=3) {
        // points.geometry.attributes.position.array[i+0] = THREE.MathUtils.lerp( points.geometry.attributes.position.array[i+0], originalPos[i+0], offset);
        // points.geometry.attributes.position.array[i+1] = THREE.MathUtils.lerp( points.geometry.attributes.position.array[i+1], originalPos[i+1], offset);
        // points.geometry.attributes.position.array[i+2] = THREE.MathUtils.lerp( points.geometry.attributes.position.array[i+2], originalPos[i+2], offset);
        const offs = offset * 40 + i;
        points.geometry.attributes.position.array[i+0] = originalPos[i+0] + Math.sin(offs + 1.2) * .5;
        points.geometry.attributes.position.array[i+1] = originalPos[i+1] + Math.sin(offs) * .5;
        points.geometry.attributes.position.array[i+2] = originalPos[i+2] + Math.sin(offs + 2.2) * .5;
    }
  }
  points.geometry.attributes.position.needsUpdate = true;
}

function createPoints(geom) {
  var material = new THREE.PointsMaterial({
    color: 0xffffff,
    size: 0.003,
    transparent: true,
    blending: THREE.AdditiveBlending,
    map: generateSprite(),
    depthWrite: false // instead of sortParticles
  });

  var cloud = new THREE.Points(geom, material);
  return cloud;
}

export default class Header extends Component {


  componentDidMount() {
    const width = window.innerWidth, height = window.innerHeight;
    
    // init
    
    const camera = new THREE.PerspectiveCamera( 70, width / height, 0.5, 3 );
    camera.position.z = 1;
    
    const scene = new THREE.Scene();
    
    const geometry = new THREE.BoxGeometry( 0.2, 0.2, 0.2 );
    
    // const mesh = new THREE.Mesh( geometry, material );
    // mesh.position.set(0, 0.2, 0);
    // scene.add( mesh );
    
    this.renderer = new THREE.WebGLRenderer( { antialias: true, alpha: true } );
    const renderer = this.renderer;
    renderer.autoClear = false;
    renderer.setClearColor(0x000000, 0.0);
    this.renderer.setSize( width, height );
    this.renderer.setAnimationLoop( this.animation.bind(this) );

    this.renderer.domElement.className = 'home-canvas'
    document.getElementById('home').prepend(this.renderer.domElement);
    this.scene = scene;
    this.camera = camera;
    this.top = 0;
    this.clock = new THREE.Clock();

    const composer = new EffectComposer( renderer );
    this.composer = composer;

    const renderPass = new RenderPass( scene, camera );
    composer.addPass( renderPass );

    const params = {
      threshold: 0,
      strength: .4,
      radius: 0,
      exposure: .5
    };

    const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
    bloomPass.threshold = params.threshold;
    bloomPass.strength = params.strength;
    bloomPass.radius = params.radius;
    
    composer.addPass( bloomPass );
    scene.background = new THREE.Color("rgba(42,0,15,1)");

    {
      const geometry = new THREE.PlaneGeometry( 100, 100 );
      const displacement = new THREE.TextureLoader().load('images/rougness.jpg')
      const material = new THREE.MeshLambertMaterial( 
        {
          color: "rgba(42,0,15,1)", 
          side: THREE.FrontSide,
          displacementMap: displacement,
          displacementScale: .1
      } );
      const plane = new THREE.Mesh( geometry, material );
      plane.position.set(0,0,-1);

      this.plane = plane;

      scene.add( plane );
    }

    // const outputPass = new OutputPass();
    // composer.addPass( outputPass );

    // const light = new THREE.AmbientLight( 0x404040 ); // soft white light
    // // light.intensity = .2;
    // scene.add( light );

    {
      const light = new THREE.PointLight( 0xff0000, 2 , 200, .5 );
      light.position.set( 0, 0, 0);
      scene.add( light );

      this.light = light
    }

    var uniforms = {};
    const loader = new GLTFLoader( );
    loader.load( 'obj/jildert.glb', ( glb ) => {
      const obj = glb.scene.children[0];
      const scale = .2;
      obj.scale.set(scale,scale,scale); 
      // obj.position.set(0, 0,0);


      obj.geometry.computeBoundingBox();

      uniforms = {
        color1: {
          value: new THREE.Color("red")
        },
        color2: {
          value: new THREE.Color("purple")
        },
        bboxMin: {
          value: obj.geometry.boundingBox.min
        },
        bboxMax: {
          value: obj.geometry.boundingBox.max
        },
        mouse: {
          value: [this.relativeMouse?.x ?? 0, this.relativeMouse?.y ?? 0]
        },
        time: {
          value: 0.0
        },
      };

      this.uniforms = uniforms;
      var material = new THREE.ShaderMaterial({
        uniforms: uniforms,
        vertexShader: `
          uniform vec3 bboxMin;
          uniform vec3 bboxMax;
        
          varying vec2 vUv;
          varying vec3 vNormal;
      
          void main() {
            vUv.y = (position.y - bboxMin.y) / (bboxMax.y - bboxMin.y);
            vNormal = normal;
            gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1.0);
          }
        `,
        fragmentShader: `
          uniform vec3 color1;
          uniform vec3 color2;
          uniform vec2 mouse;
          uniform float time;
        
          varying vec2 vUv;
          varying vec3 vNormal;
          
          void main() {
            float distance = distance(vNormal.xy, mouse);
            vec4 color = vec4(mix(color1, color2, vUv.y), 1.0) * distance;
            // color.a = clamp(time * 0.2, 0.0, 1.0);
            gl_FragColor = color;
          }
        `,
        wireframe: true,
        transparent: true,
      });

      obj.material = material;
      // obj.position.set(0, 0.2, 0);
      this.mesh = obj;
      scene.add( obj );


      const points = createPoints(obj.geometry.clone());
      obj.add(points);

      this.points = points;
      // points.scale.set(scale,scale,scale); 
      // scene.add(points);
    });

    window.addEventListener('resize', () => {
      const width = window.innerWidth, height = window.innerHeight;
      this.renderer.setSize( width, height );
      this.camera.aspect = width / height;
      this.camera.updateProjectionMatrix();
    })

    window.addEventListener('scroll', (e) => {
      this.top = window.scrollY;
    })
    window.addEventListener('mousemove', (e) => {
      this.mouse = e;
      this.relativeMouse = {
        x: e.pageX / this.renderer.domElement.width,
        y: e.pageY / this.renderer.domElement.height,
      }
      if (uniforms.mouse) {
        uniforms.mouse.value.x = this.relativeMouse.x;
        uniforms.mouse.value.y = this.relativeMouse.y;
      }
    })
  }

  animation( time ) {
    // if (this.uniforms?.time) {
    //   this.uniforms.time.value = (time / 1000) - 5.0;
    // }
    const delta = this.clock.getDelta();
    const lerp = delta * 10;
    if (this.mesh) {
      this.mesh.rotation.x =  THREE.MathUtils.lerp(this.mesh.rotation.x, this.top / window.innerHeight * Math.PI, lerp);

      if (this.mouse) {
        this.mesh.rotation.y = THREE.MathUtils.lerp(this.mesh.rotation.y, this.mouse.pageX / window.innerWidth * Math.PI + Math.PI, lerp);

        const mouse = new THREE.Vector3();

        mouse.x = ( this.mouse.x / window.innerWidth ) * 2 - 1;
        mouse.y = - ( this.mouse.y / window.innerHeight ) * 2 + 1;
        const raycaster = new THREE.Raycaster();
        raycaster.setFromCamera( mouse, this.camera );   
        const objects = raycaster.intersectObjects([this.mesh, this.plane], false);
        const intersect = objects[1] || objects[0];
        if (intersect) this.light.position.set(intersect.point.x, intersect.point.y, 0);
      }
      else {
        this.mesh.rotation.y = THREE.MathUtils.lerp(this.mesh.rotation.y, time / 10000 + Math.PI, lerp);
      }
    }
    if (this.top < window.innerHeight){

      if (this.points) {
        updatePointsGeometry(this.points, time * .0000025)
        // this.points.material.opacity = Math.clamp()
      }

      // this.renderer.render( this.scene, this.camera );
      this.composer.render();
    }
  }

  render() {
    let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
      
      <header id="home">
         <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
          <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
            <ul id="nav" className="nav">
               <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
               <li><a className="smoothscroll" href="#about">About</a></li>
             {/* <li><a className="smoothscroll" href="#resume">Resume</a></li */}
               <li><a className="smoothscroll" href="#portfolio">Projects</a></li>
               {/* <li><a className="smoothscroll" href="#testimonials">Testimonials</a></li>
               <li><a className="smoothscroll" href="#contact">Contact</a></li> */}
            </ul>
         </nav>

         <div className="banner">
            <div className="banner-text">
               <h1 className="responsive-headline">Jildert Venema</h1>
               <h3 style={{color:'#fff', fontFamily:'sans-serif '}}>
                  Full Stack Software Developer
               </h3>
               <hr/>
               <ul className="social">
                  {
                    resumeData.socialLinks && resumeData.socialLinks.map(item =>{
                      return(
                              <li key={item.name}>
                                <a href={item.url} target="_blank"><i className={item.className}></i></a>
                              </li>
                            )
                          }
                    )
                    
                  }
                  {/* <li>
                    <a href="https://codesandbox.io/u/jildertvenema" target="_blank">
                      <svg className="codeSandbox-svg" aria-label="CodeSandbox" role="presentation" x="0px" y="0px" width="35px" height="30px" viewBox="0 0 452 452"><path fill-rule="evenodd" clip-rule="evenodd" d="M226 228.286V447.293C229.813 447.293 232.319 446.466 235.742 444.51L411.107 344.302C418.031 340.333 420.855 334.19 420.855 326.208V123.008C420.855 119.004 420.011 116.609 418.066 113.266L231.61 218.619C228.141 220.601 226 224.29 226 228.286ZM323.425 354.044C323.425 359.611 321.337 362.395 316.466 365.178L258.011 398.581C253.836 401.365 248.269 399.973 248.269 394.406V245.485C248.269 241.501 251.775 236.338 255.227 234.351L388.839 157.803C392.55 155.667 395.797 159.088 395.797 163.37V242.701C395.797 246.814 393.859 250.509 390.23 252.444L330.384 284.455C326.755 286.39 323.425 290.085 323.425 294.197V354.044Z" fill="#B8B9BA"></path><path fill-rule="evenodd" clip-rule="evenodd" d="M31.1505 326.208V123.008C31.1505 115.017 35.3465 107.488 42.2848 103.523L212.082 7.49014C215.74 5.55143 221.825 4.7066 226 4.7066C230.175 4.7066 236.617 5.74065 239.918 7.49017L408.324 103.523C411.656 105.492 416.181 110.027 418.066 113.266L231.567 219.041C228.098 221.023 226 224.788 226 228.784V447.293C222.187 447.293 218.289 446.466 214.866 444.51L43.6766 345.693C36.7382 341.729 31.1505 334.2 31.1505 326.208ZM56.2026 163.37V242.701C56.2026 248.269 57.5944 251.052 63.1615 253.836L121.616 287.238C127.184 290.022 128.575 294.197 128.575 298.373V354.044C128.575 359.611 129.967 362.395 135.534 365.178L193.989 398.581C199.556 401.364 203.732 399.973 203.732 394.406V245.485C203.732 241.31 202.34 237.134 196.773 234.351L65.9451 159.194C61.7697 156.411 56.2026 157.803 56.2026 163.37ZM284.455 68.7286L232.959 97.956C228.784 100.74 223.217 100.74 219.041 97.956L167.545 68.7286C164.155 66.8127 159.806 66.8225 156.411 68.7286L92.3889 104.915C86.8218 107.699 86.8218 113.266 92.3889 116.049L220.433 189.814C223.86 191.776 228.14 191.776 231.567 189.814L359.611 116.049C363.787 113.266 365.178 107.699 359.611 104.915L295.589 68.7286C292.194 66.8225 287.845 66.8127 284.455 68.7286Z" fill="#F2F2F2"></path></svg>
                    </a>
                  </li> */}

               </ul>
            </div>
         </div>


        {/* <div className='row quick-buttons'>
          <a className='quick-button' href="https://csb-tflhc.netlify.app/" target='_blank'>
            Chess Bot
          </a>
          <a className='quick-button' href="https://chat.jildert.dev/" target='_blank'>
            Chat Bot
          </a>
        </div> */}

         <p className="scrolldown">
            <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
         </p>

      </header>
      </React.Fragment>
    );
  }
}

